import { Breadcrumb, Dropdown, Menu } from 'antd';
import useBreadcrumd from "./useBreadcrumd"
import { useNavigate } from 'react-router-dom';
import {
    FullscreenExitOutlined,
    FullscreenOutlined,
    FontSizeOutlined,
    FontColorsOutlined
} from "@ant-design/icons"
import { Row, Col } from "antd"
import screenfull from "screenfull"
import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { switchSize, switchLang } from '../../store/actions';
export default () => {

    const a = useBreadcrumd();
    const navigate = useNavigate();
    const [full, setFull] = useState(false);
    const { size, lang } = useSelector(state => state.app);
    const dispatch = useDispatch()
    useEffect(() => {
        screenfull.onchange(ev => {
            setFull(screenfull.isFullscreen)
        })
    }, [])



    const isSkip = (route, idx) => {
        return (route.path && idx !== a.length - 1)
    }


    const skipTo = (route, idx) => {
        if (isSkip) {
            navigate(route.path, { replace: true })
        }
    }


    const renderBreadItem = () => {
        return (a.map((ele, idx) =>
            <Breadcrumb.Item key={ele.key}
                onClick={() => skipTo(ele, idx)}
            >
                <span
                    style={{
                        color: isSkip(ele, idx) ? "#fff" : "rgb(196,188,188)",
                        cursor: isSkip(ele, idx) ? "pointer" : "default"
                    }}
                >{ele.label}</span>
            </Breadcrumb.Item>
        ))
    }

    const toggleFull = () => {
        if (screenfull.isEnabled) {
            screenfull.toggle()
        }
    }

    const sizes = ["large", "middle", "small"];
    const menu = (

        {
            items:
                sizes.map(item => ({
                    key: item,
                    label: <div style={{ color: size === item ? "red" : "black" }}
                        onClick={() => toggleSize(item)}
                    >{item}</div>
                }))

        }

    )
    const langs = [
        { value: "zh", label: "简体中文" },
        { value: "en", label: "English" },
    ]
    const toggleSize = (size) => {
        dispatch(switchSize(size))
    }
    const toggleLang = (lang) => {
        dispatch(switchLang(lang));
        console.log(lang);
    }
    return (

        <div className='ml-header'>
            <Row>
                <Col span={18}>
                    <Breadcrumb>
                        {renderBreadItem()}
                    </Breadcrumb>
                </Col>
                <Col span={3} offset={3}>
                    <div className='icons'>
                        {
                            !full
                                ? <FullscreenOutlined onClick={toggleFull} />
                                : <FullscreenExitOutlined onClick={toggleFull} />

                        }
                        <Dropdown
                            menu={
                                {
                                    items:
                                        langs.map(item => ({
                                            key: item.value,
                                            label: (
                                                <div
                                                    style={{ color: lang === item.value ? "red" : "black" }}
                                                    onClick={() => toggleLang(item.value)}
                                                >{item.label}</div>
                                            )
                                        }))
                                }
                            }


                            placeMent="bottomRight"
                            arrow={{ pointAtCenter: true }}

                        >
                            < FontColorsOutlined />
                        </Dropdown>

                        <Dropdown
                            menu={menu}
                            placeMent="bottomRight"
                            arrow={{ pointAtCenter: true }}
                            trigger={["click"]}
                        >
                            <FontSizeOutlined />
                        </Dropdown>

                    </div>

                </Col>
            </Row>
        </div >


    )
}


