
import {
    MenuFoldOutlined,
    MenuUnfoldOutlined,
    UploadOutlined,
    UserOutlined,
    VideoCameraOutlined,
} from '@ant-design/icons';
import { Outlet } from 'react-router-dom';
import { ConfigProvider,Layout, Menu} from 'antd';
import React, { useState,useEffect } from 'react';
import "./style.scss"
import MlSider from "./MlSider"
import MlHeader from './MlHeader';
import { useSelector } from 'react-redux';
import zhCN from 'antd/es/locale/zh_CN';
import enGB from 'antd/es/locale/en_GB';
import { IntlProvider } from 'react-intl';

import zhCNML from "@/locales/zh_CN" 
import enGBML from "@/locales/en_GB" 
import Mlcontent from "../layout/Mlcontent"
import 'antd/dist/antd.variable.min.css';
const langs={
    zh:zhCN,
    en:enGB
}

const langsML={
    zh:zhCNML,
    en:enGBML
}

const { Header, Sider, Content } = Layout;

export default (props) => {

    const {size,lang,color} = useSelector(state=>state.app);
    const [collapsed, setCollapsed] = useState(false);

    useEffect(() => {
        ConfigProvider.config({
          theme: {
            primaryColor: color
          }
        })
      }, [color])


    // const resizeHandler = (ev)=>{
    //     setCollapsed(ev.srcElement.innerWidth<=800)
    // }
    // useEffect(()=>{
    //     window.addEventListener("resize",resizeHandler);
    //     return ()=>{
    //         window.removeEventListener("resize",resizeHandler)
    //     }
    // },[])

    return (
        <ConfigProvider componentSize={size} locale={langs[lang]}>
            <IntlProvider messages={langsML[lang]} locale={lang}>
        <div className='ml-layout'>
            <Layout>
                <Sider trigger={null} collapsible collapsed={collapsed}>
                    
                    <MlSider value={collapsed} onChange={() => setCollapsed(!collapsed)}/>
                </Sider>
                <Layout className="site-layout">
                    <Header
                        className="site-layout-background"
                        style={{
                            padding: 0,
                        }}
                    >
                        <MlHeader/>
                    </Header>
                    <Content
                        className="site-layout-background"
                        style={{
                            margin: '24px 16px',
                            padding: 24,
                            minHeight: 280,
                          }}
                    >
                        <Mlcontent/>
                    </Content>
                </Layout>
            </Layout>
        </div>
        </IntlProvider>
        </ConfigProvider>
    )
}
