

import { Outlet } from "react-router-dom"
import { SettingOutlined } from "@ant-design/icons"
import { useState } from "react";
import { Drawer } from "antd";
import { FormattedMessage } from "react-intl";
import { TwitterPicker } from "react-color";
import { useSelector ,useDispatch} from "react-redux";
import { switchColor } from "../../store/actions";

export default () => {
    const {color} = useSelector(state=>state.app);
    const dispatch = useDispatch()
    const [show, setShow] = useState(false);
    const close=()=>{
        setShow(false)
    }
    const changeColor=(e)=>{
        console.log(e);
        dispatch(switchColor(e.hex))
    }
    return (
        <div className="ml-content">
            <Outlet></Outlet>

            <div className="setting" onClick={()=>setShow(true)}
             style={{ background: 'var(--ant-primary-color)' }}
            >
                <SettingOutlined />
            </div>

            <Drawer
                title={<FormattedMessage id="setting.title"/>}
                placement="right"
                closable={true}
                onClose={close}
                open={show}
                width={330}
            >
                <TwitterPicker color={color} onChangeComplete={changeColor}/>
            </Drawer>
        </div>
    )
}